.aktuelno,
.s-work {
    padding-top: 3rem;
    padding-bottom: 2rem;
}
.aktuelno {
    background: $black_color;
}
.s-work {
    @include background-light;
    padding-bottom: 2rem;
}

.aktuelno__title,
.s-work__title {
    margin: 0;
    font-weight: normal;
    text-align: center;
    color: $black_color; 
    text-align: center;
    @include front-page-title; 
    margin-bottom:3rem;  
    letter-spacing: 0.1rem; 
    @media screen  and (min-width: $medium_screen){
        font-size: 2rem;
    }
    @media screen  and (min-width: $large_screen){
        font-size: 2.3rem;
    }
}

.s-work__title {
    color: $red_dark;
}

.aktuelno__posts,
.s-work__posts {
    display:flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-start;  
}

.news-card {
    max-width: 300px;
    margin: 0 0.5rem;
    margin-bottom: 2rem;
    opacity: 1; 
    @media screen and (min-width: $large_screen) {
        opacity: 0; 
        @include slide-from-animation;
    }
    @media screen  and (min-width: $medium_screen){
        max-width: 380px;
    }
    img {
        object-fit: cover; 
        margin: 0 auto;
        border-radius: 2%;
        @media screen  and (min-width: $medium_screen){
            max-width: 380px;
            filter: sepia(20%);
            transition: all 0.2s ease-in;
            &:hover {
                filter: sepia(0%);
                box-shadow: 1px 1px 13px rgba(0, 0, 3, 0.5);
            }
        }
    }
    
    @media screen  and (min-width: $small_screen){
        margin: 0 1em;
        margin-bottom: 2rem;
    }
    
}

.news-card-content {
    p {
        display:none;
        @media screen  and (min-width: $medium_screen){
            display: block;
        }
    }
}

.news-card-title {
    margin:unset;
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    min-height: 20px; 
    line-height: 1.5rem;
    font-weight: bold;
    letter-spacing: 0.05rem;
    @media screen  and (min-width: $large_screen){
        line-height: 1.8rem;
        margin-top: 2rem; 
    }
 }

.light-color {
    color:$light_color;  
    a,
    p {
        color:$light_color;  
    }
    img {
        height: 200px;
        width: 320px;
        box-shadow: none;
        @media screen  and (min-width: $medium_screen){
            height: 240px;
            min-width: 380px;           
        }
    }
}

.selected {
    background-color: antiquewhite;
    color:$black_color;
}

.font-white {
    color: $light_color; 
}

.large-title {
    display: block;
    font-size: 2rem;
    line-height: 2.5rem;
    margin-bottom: 1rem;
    color:antiquewhite;
}

.campagns {
    background: $red_dark;
    background-image: url("http://localhost/star/wp-content/themes/star/images/campagn_mobile.png");
    background-size: cover;
    background-repeat: no-repeat;

    @media screen and (min-width: $large_screen) {
        background-image: url("http://localhost/star/wp-content/themes/star/images/campagn_large.png");
    }  
}

.news-card-content {
    display: flex;
    flex-direction: column;

}

.kampanja {
    @media screen and (min-width: $large_screen) {
        border-left: 1px solid antiquewhite;
        padding-left: 2rem;
    }  
    
}

@for $i from 1 through 9 {
	.news-card {
		&:nth-of-type(#{$i}) {
			// Delay the animation. Delay increases as items loop.
			transition-delay: $i * (0.1s);
			}
	}
}



